import React, { Component } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  Form,
  FormGroup,
  Input,
  Label,
} from "reactstrap";
import PropTypes from "prop-types";

const defaultState = {
  email: "",
  password: "",
  error: {},
  isLoading: false,
};

class Login extends Component {
  state = defaultState;

  changeHandler = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  submitHandler = async (e) => {
    e.preventDefault();

    console.log(this.state);
  };

  render() {
    let { isOpen, loginCloseHandler } = this.props;
    let { email, password, error, isLoading } = this.state;

    return (
      <section id="login-page">
        <Form onSubmit={this.submitHandler}>
          <FormGroup>
            <Label for="email">
              Email<span className="text-danger">*</span>
            </Label>
            <Input
              type="text"
              placeholder="Type here..."
              onChange={this.changeHandler}
              name="email"
              value={email}
              required
            />
          </FormGroup>
          <FormGroup>
            <Label for="email">
              Password<span className="text-danger">*</span>
            </Label>
            <Input
              type="password"
              placeholder="Type here..."
              onChange={this.changeHandler}
              name="password"
              value={password}
              required
            />
          </FormGroup>

          <Button
            color=""
            className="site-btn-dark"
            block
            type="submit"
            disabled={isLoading}
          >
            Login
          </Button>
        </Form>
      </section>
    );
  }
}

Login.propTypes = {
  isOpen: PropTypes.bool,
  loginCloseHandler: PropTypes.func.isRequired,
};
export default Login;
