import React, { useState } from 'react';
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';


import {
    Container,
    Collapse,
    Navbar,
    NavbarToggler,
    Nav,
    NavItem,

} from 'reactstrap';


import logo from '../../assets/img/logo.png'

const TopHeader = ({ history, wheelPosition: { clientY, deltaY } }) => {

    const [isOpen, setIsOpen] = useState(false);


    const toggle = () => setIsOpen(!isOpen);


    return (
        <header id="header">
            <div className={(clientY > 100 && deltaY > 100) ? 'sticky-nav nav-wrapper' : 'nav-wrapper'} >
                <Container className='p-0'>
                    <Navbar expand="md p-0">
                        <Link className='text-dark navbar-brand' to='/'>
                            <img src={logo} alt='logo' className='site-logo' /> Arrot Krishi Ponno Limited
                        </Link>
                        <NavbarToggler onClick={toggle}><i className='fa fa-bars' /></NavbarToggler>
                        <Collapse isOpen={isOpen} navbar>
                            <Nav className="ml-auto" navbar>
                                <NavItem>
                                    <Link to="/about" className="nav-link" style={{ color: 'red' }}>About</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to="/growers" className="nav-link" style={{ color: 'red' }}>Growers</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to="/buyers" className="nav-link" style={{ color: 'red' }}>Buyers</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to="/why-arrot" className="nav-link" style={{ color: 'red' }}>Why Arrot Vegitables</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to="/team" className="nav-link" style={{ color: 'red' }}>Team</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to="/contact-us" className="nav-link" style={{ color: 'red' }}>Contact Us</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to="/login" className="nav-link" style={{ color: 'red' }}>Login</Link>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </Navbar>
                </Container>
            </div>
        </header>
    )
}

TopHeader.propTypes = {
    history: PropTypes.object.isRequired,
    wheelPosition: PropTypes.object
}
export default TopHeader